// Variables
$primary-color: #4caf50;
$secondary-color: #ffffff;
$user-bg-color: #007bff;
$assistant-bg-color: #e1e1e1;
$message-border-radius: 15px;
$chat-bg-color: #f5f5f5;
$input-border-color: #ccc;
$input-focus-border-color: $primary-color;
$button-bg-color: #007bff;
$button-hover-bg-color: darken($button-bg-color, 10%);

// Chat App container styling
.chat-app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: $chat-bg-color;
}

.chat-messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}
.chat-message {
  display: flex;
  align-items: center;
  margin: 10px 0;
  max-width: 90%;
  word-wrap: break-word;

  &.user {
    justify-content: flex-end;
    align-self: flex-end;

    .message-text {
      background-color: #007bff;
      color: #ffffff;
    }
  }

  &.assistant {
    justify-content: flex-start;

    .message-text {
      background-color: #e1e1e1;
    }
  }

  .message-text {
    padding: 10px 15px;
    border-radius: 15px;
  }

  audio {
    margin-top: 10px;
    width: 100%;
  }
}

// Chat Input styling
.chat-input-container {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid $input-border-color;
  background-color: $secondary-color;
}

.chat-input {
  flex: 1;
  padding: 10px;
  margin: 0 10px;
  border: 1px solid $input-border-color;
  border-radius: 8px;
  font-size: 16px;

  &:focus {
    outline: none;
    border-color: $input-focus-border-color;
  }
}

.send-button {
  background-color: $button-bg-color;
  color: $secondary-color;
  border: none;
  border-radius: 50%;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: $button-hover-bg-color;
  }
}

.action-buttons {
  display: flex;
  flex-direction: column;
  margin-top: 10px;

  button {
    background-color: $button-bg-color;
    color: $secondary-color;
    border: none;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: $button-hover-bg-color;
    }
  }
}

.radio-buttons {
  display: flex;
  flex-direction: column;
  margin-top: 10px;

  label {
    display: flex;
    align-items: center;
    margin-bottom: 5px;

    input[type="radio"] {
      margin-right: 10px;
    }
  }
}

.audio-recorder {
  display: flex;
  flex-direction: column;
  margin-top: 10px;

  button {
    background-color: $button-bg-color;
    color: $secondary-color;
    border: none;
    border-radius: 8px;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: $button-hover-bg-color;
    }
  }
}
